import React, { Component } from "react";
import { Pagination } from "antd";
import MovieService from "../MovieService/MovieService";
import MovieList from "../MovieList/MovieList";
import Spin from "../Spin/Spin";
import Alert from "../Alert/Alert";
import MovieSearch from "../MovieSearch/MovieSearch";
import "../default-style/default-style.css";

export default class MovieApp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      movies: [],
      loading: true,
      error: false,
      isOnline: navigator.onLine,
      query: "",
      searchPerformed: false,
      currentPage: 1, //      
      totalPages: 0, //      
    };
    this.setMovies = this.setMovies.bind(this);
    this.setError = this.setError.bind(this);
    this.handleOnline = this.handleOnline.bind(this);
    this.handleOffline = this.handleOffline.bind(this);
    this.handleSearchQueryChange = this.handleSearchQueryChange.bind(this);
    this.handlePageChange = this.handlePageChange.bind(this);
  }

  componentDidMount() {
    window.addEventListener("online", this.handleOnline);
    window.addEventListener("offline", this.handleOffline);
  }

  componentWillUnmount() {
    window.removeEventListener("online", this.handleOnline);
    window.removeEventListener("offline", this.handleOffline);
  }

  handleOnline() {
    this.setState({ isOnline: true });
  }

  handleOffline() {
    this.setState({ isOnline: false });
  }

  handleSearchQueryChange(query) {
    this.setState({ query, loading: true, searchPerformed: true });
  }

  handlePageChange(page) {
    this.setState({ currentPage: page, loading: true }, () => {
      //  fetchMovies     
      const { query, currentPage } = this.state;
      this.movieService.fetchMovies(query, currentPage);
    });
  }

  setMovies(movies, totalPages) {
    this.setState({ movies, totalPages, loading: false, error: false });
  }

  setError(error) {
    this.setState({ error, loading: false });
  }

  render() {
    const {
      movies,
      loading,
      error,
      isOnline,
      query,
      searchPerformed,
      currentPage,
      totalPages,
    } = this.state;

    return (
      <div className="container">
        {isOnline ? (
          <>
            <MovieSearch onQueryChange={this.handleSearchQueryChange} />
            {error && (
              <Alert
                type="error"
                message=", -   !"
                description={error.message}
              />
            )}
            {loading && <Spin />}
            <MovieService
              //     MovieService
              ref={(movieService) => {
                this.movieService = movieService;
              }}
              setMovies={this.setMovies}
              setError={this.setError}
              searchWord={query}
            />

            {searchPerformed && movies.length === 0 && !error && !loading && (
              <Alert
                description=""
                message="  "
                type="info"
              />
            )}

            {!error && movies.length > 0 && (
              <>
                <MovieList movies={movies} />
                <Pagination
                  style={{
                    display: "flex",
                    justifyContent: "center",
                    marginBottom: "17px",
                    marginTop: "36px",
                  }}
                  defaultCurrent={currentPage}
                  total={totalPages}
                  pageSize={10}
                  onChange={this.handlePageChange}
                />
              </>
            )}
          </>
        ) : (
          <Alert
            style={{
              display: "flex",
              justifyContent: "center",
              marginBottom: "17px",
            }}
            type="error"
            description=""
            message="   ."
          />
        )}
      </div>
    );
  }
}
